<template>
  <section class="container-fluid mt-5">
    <div class="d-flex justify-content-center">
      <el-input class="w-50" prefix-icon="el-icon-search" size="mini" placeholder="Nombre" v-model="filterForm.query"
        v-on:change="handleSearchFilter()" clearable></el-input>
      <el-button class="ms-2" @click.stop.prevent="handleSearchFilter" size="mini" type="primary" icon="el-icon-search">
        Buscar
      </el-button>
      <el-button class="ms-2" @click.stop.prevent="handleClean" size="mini" type="warning" icon="el-icon-delete">Limpiar
      </el-button>
    </div>
    <div class="d-flex justify-content-end mt-4">
      <el-pagination @size-change="handleChangeItemPage" :page-sizes="[25, 50, 100, 200]" :page-size="25"
        layout="total,sizes,prev, pager, next" @current-change="handleCurrentChange" :total="parseInt(totalPages)">
      </el-pagination>
    </div>
    <div class="row my-4">
      <div class="col-2 d-flex justify-content-center mb-3" v-for="(img, index) in list" :key="img">
        <a type="button" @click="openImage(img, index)">
          <el-image class="img-thumbnail bg-light hvr-grow-shadow" style="width: 9rem; height: 9rem;"
            :src="configStore.baseUrl + img.pathFile" fit="cover">
          </el-image>
        </a>
        <el-dialog title="Detalles de la imagen" custom-class="shadow-none rounded" :visible.sync="detailsImage">
          <div class="container">
            <div class="row">
              <div class="col-7 py-4">
                <img class="img-fluid bg-secondary" :src="configStore.baseUrl + imgSelected.pathFile" alt="img.name">
              </div>
              <div class="col-5 d-flex flex-column justify-content-center py-4">
                <span class="mb-2"><span class="fw-bold">Nombre del archivo: </span>{{imgSelected.name}}</span>
                <span class="mb-2"><span class="fw-bold">Tipo de archivo: </span>{{imgSelected.extension}}</span>
                <span class="mb-2"><span class="fw-bold">Tama√±o del archivo: </span>{{imgSelected.size}} B</span>
                <span class="mb-2"><span class="fw-bold">Dimensiones: </span>{{imgSelected.width}} x {{imgSelected.height}}
                  px</span>
                <span class="mb-2"><span class="fw-bold">Ruta: </span>{{imgSelected.pathFile}}</span>
                <el-button class="d-block mx-auto mt-3" @click="handleDelete(indexImg, imgSelected)" size="mini"
                  type="danger" icon="el-icon-delete">
                  Eliminar
                </el-button>
              </div>
            </div>
          </div>
        </el-dialog>
      </div>
    </div>
    <div class="d-flex justify-content-end">
      <el-pagination @size-change="handleChangeItemPage" :page-sizes="[25, 50, 100, 200]" :page-size="25"
        layout="total,sizes,prev, pager, next" @current-change="handleCurrentChange" :total="parseInt(totalPages)">
      </el-pagination>
    </div>
  </section>
</template>

<script>
import { search, destroy, getPage } from '@/api/images'
import { useStoreConfig } from '../../stores/configStore'

const configStore = useStoreConfig()

export default {
  data () {
    return {
      filterForm: {
        query: '',
        initDate: '',
        endDate: ''
      },
      pickerOptions: {
        shortcuts: [{
          text: 'Semana pasada',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'Mes pasado',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '3 Meses atr√°s',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '6 Meses atr√°s',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 180)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '9 Meses atr√°s',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 270)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '1 A√±o atr√°s',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 365)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      configStore,
      totalPages: '',
      totalItemPage: '',
      currentPage: '',
      list: null,
      loading: false,
      value: '',
      filterDateRegisterRange: '',
      sizePerPage: 25,
      detailsImage: false,
      imgSelected: {},
      indexImg: 0
    }
  },
  mounted () {
    this.handleSearchFilter()
  },
  methods: {
    async handleCurrentChange (page) {
      this.currentPage = page
      this.loading = true
      await getPage(page, this.sizePerPage)
        .then(response => {
          this.list = response.data.data
          this.totalPages = response.data.total
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async handleChangeItemPage (val) {
      this.loading = true
      this.sizePerPage = val
      await getPage(this.currentPage, val)
        .then(response => {
          this.list = response.data.data
          this.totalPages = response.data.total
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async handleSearchFilter () {
      this.loading = true
      await search(this.filterForm)
        .then((response) => {
          this.list = response.data.data
          this.totalPages = response.data.total
        })
      /* await search(this.filterForm)
        .then((response) => {
          this.list = response.data.data
          this.totalPages = response.data.total
          console.log(response)
        })
        .catch(this.responseCatch) */
        .finally(() => (this.loading = false))
    },
    handleFilterDateRegister () {
      if (this.filterDateRegisterRange) {
        this.filterForm.initDate = this.filterDateRegisterRange[0]
        this.filterForm.endDate = this.filterDateRegisterRange[1]
        this.handleSearchFilter()
      } else {
        this.filterForm.initDate = ''
        this.filterForm.endDate = ''
        this.handleSearchFilter()
      }
    },
    handleClean () {
      this.filterForm.query = ''
      this.filterForm.initDate = ''
      this.filterForm.endDate = ''
      this.handleSearchFilter()
    },
    handleDelete (index, row) {
      this.$confirm('Se borrara la imagen en el servidor, ¬øEsta seguro?', 'Cuidado', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancelar',
        type: 'warning'
      })
        .then(() => {
          destroy(row._id)
            .then(response => {
              this.$message({
                showClose: true,
                message: 'Imagen eliminada exitosamente.',
                type: 'success'
              })
              this.list.splice(index, 1)
            })
            .catch(() => {
              this.$message({
                showClose: true,
                message: 'Error al eliminar la imagen.',
                type: 'error'
              })
            })
            .finally()
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operaci√≥n'
          })
        })
    },
    openImage (image, index) {
      this.detailsImage = true
      this.imgSelected = image
      this.indexImg = index
    }
  }
}
</script>
<style>
.hvr-grow-shadow {
  box-shadow: 0 0 0.1rem rgba(0, 0, 0, 0);
  transition-duration: 0.2s;
  transition-property: box-shadow, transform;
}
.hvr-grow-shadow:hover {
  box-shadow: 0.5rem 0.5rem 0.25rem rgba(0, 0, 0, 0.2);
  transform: scale(1.1);
}
.el-dialog__header{
  border-bottom: 1px solid #dee2e6;
}
.el-dialog__title{
  font-weight: 700;
}
.el-dialog__body{
  padding: 0;
}
</style>
